@import "~normalize.css/normalize.css";
@import "~swiper/css/swiper.css";
body.theme--main {
  --primary-color: #a67ee5;
  --secondary-color: #5f458c;
}
body.theme--purple {
  --primary-color: #5f458c;
  --secondary-color: #a67ee5;
}
body.theme--blue {
  --primary-color: #a0ceff;
  --secondary-color: #3e72ee;
}
body.theme--light-blue {
  --primary-color: #3e72ee;
  --secondary-color: #a0ceff;
}

@keyframes scale-in-bottom {
  0% {
    transform: scale(0);
    transform-origin: 50% 100%;
    opacity: 1;
  }
  100% {
    transform: scale(1);
    transform-origin: 50% 100%;
    opacity: 1;
  }
}
@keyframes floating-flamingo {
  0% {
    transform: translate3d(0, 0, 0) rotate3d(1, 1, 1, 0);
  }
  50% {
    transform: translate3d(0, 20px, 0) rotate3d(1, 1, 1, -12deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate3d(1, 1, 1, 0);
  }
}
@keyframes floating-leafs {
  0% {
    opacity: 0;
    transform: rotate(-40deg);
  }
  10% {
    opacity: 1;
    transform: rotate(20deg);
  }
  30% {
    transform: rotate(-30deg);
  }
  50% {
    transform: rotate(10deg);
  }
  70% {
    transform: rotate(-8deg);
  }
  100% {
    transform: rotate(0);
  }
}
@keyframes floating-blue-leaves-left {
  0% {
    opacity: 0;
    transform: rotate(60deg);
  }
  20% {
    opacity: 1;
    transform: rotate(-30deg);
  }
  40% {
    transform: rotate(15deg);
  }
  60% {
    transform: rotate(-8deg);
  }
  80% {
    transform: rotate(5deg);
  }
  90% {
    transform: rotate(-3deg);
  }
  100% {
    transform: rotate(0);
  }
}
@keyframes floating-blue-leaves-right {
  0% {
    opacity: 0;
    transform: rotate(-45deg);
  }
  20% {
    opacity: 1;
    transform: rotate(10deg);
  }
  40% {
    transform: rotate(-20deg);
  }
  60% {
    transform: rotate(5deg);
  }
  80% {
    transform: rotate(-2deg);
  }
  100% {
    transform: rotate(0);
  }
}
@keyframes roll-in-left {
  0% {
    transform: translateX(-150px) rotate(-540deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
}
@keyframes roll-in-right {
  0% {
    transform: translateX(150px) rotate(540deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes animate-to {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  80% {
    opacity: 0.5;
  }
  100% {
    transform: translateX(-50px);
    opacity: 0;
  }
}
@keyframes animate-from {
  0% {
    transform: translateX(50px);
    opacity: 0;
  }
  80% {
    opacity: 0.5;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes zoom-out {
  from {
    transform: scale(1.2);
  }
  to {
    transform: scale(1);
  }
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes translate-in-right {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0%);
  }
}
