.result {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 15rem 5.7rem 6rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 150px 57px 60px;
  }

  @media (orientation: portrait) {
    padding: 8rem 0 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 8rem 0 3rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 3rem 0;
  }
}

.result__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: -1;

  @media (orientation: portrait) {
    left: -1rem;
    right: -1rem;
  }

  picture {
    display: block;
    width: 100%;
    height: 100%;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

}

.result__image-item {
  position: absolute;
}

.result__image-item--suitcase {
  bottom: 11rem;
  width: 26rem;
  z-index: 3;

  @media (max-width: $mobile) {
    width: 18rem;
  }
}

.result__image-item--flamingo {
  width: 18rem;
  bottom: 13rem;
  margin-left: -13rem;
  transform: translateY(250px) rotate(-45deg);
  opacity: 0;
  transition: transform 0.4s ease-out, opacity 0.4s ease-out 0.1s;
  z-index: 4;

  @media (max-width: $mobile) {
    width: 14rem;
    margin-left: -10rem;
  }
}

.result__image-item--leaf-green {
  bottom: 16rem;
  margin-left: 30rem;
  z-index: 2;
  opacity: 0;
  transform-origin: left center;
  width: 25rem;

  @media (max-width: $mobile) {
    margin-left: 20rem;
    width: 13rem;
  }
}

.result__image-leaves--left {
  bottom: 13rem;
  left: 50%;
  margin-left: -25rem;
  transform-origin: right center;
  width: 20rem;

  @media (max-width: $mobile) {
    margin-left: -14rem;
    width: 12rem;
  }

}


.result__image-leaves--right {
  bottom: 16rem;
  right: 50%;
  margin-right: -25rem;
  transform-origin: left center;
  width: 20rem;

  @media (max-width: $mobile) {
    margin-right: -14rem;
    width: 12rem;
  }
}

.result__title {
  margin: 0 auto 2rem;
  color: $c-light;
  animation: zoom-out 500ms ease-out both;


  @media (min-width: $stop-scaling) {
    margin: 0 auto 20px;
  }

  @media (orientation: portrait) {
    margin-bottom: 1.5rem;
  }

  svg {
    display: block;
    width: 100%;
    height: 100%;
    stroke-width: 0.2rem;
    stroke-linejoin: round;
  }
}

.result__text {
  margin-bottom: 2rem;
  text-align: center;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.01em;

  @media (min-width: $stop-scaling) {
    margin-bottom: 20px;
    font-size: 48px;
  }

  @media (orientation: portrait) {
    font-size: 2.8rem;
    line-height: 1.1;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2.8rem;
  }

  p {
    margin: 0;
  }
}

.result__button {
  @include btn-reset;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  margin: auto auto 1rem;
  min-width: 34rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: auto auto 10px;
    min-width: 340px;
    font-size: 24px;
  }

  @media (orientation: portrait) {
    margin: auto 0 0;
    transform: none;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    transform: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin: auto 0 0;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }


  &-text {
    .screen--result.screen--show & {
      animation: fade-in 0.4s ease-out both;
    }
  }

  &-icon {
    position: relative;
    margin-left: 1.5rem;
    width: 4rem;
    height: 4rem;
    color: $c-dark;

    .screen--result.screen--show & {
      animation: roll-in-right 0.4s ease-out both;
    }

    @media (min-width: $stop-scaling) {
      margin-left: 15px;
      width: 40px;
      height: 40px;
    }

    &::before {
      @include center;
      content: "";
      width: 100%;
      height: 100%;
      background-color: $c-light;
      border: 2px solid $c-light;
      border-radius: 50%;
      z-index: -1;
    }
  }

  svg {
    @include center;
    margin-left: 0.3rem;
    width: 2.4rem;
    height: 1.9rem;

    @media (min-width: $stop-scaling) {
      margin-left: 3px;
      width: 24px;
      height: 19px;
    }
  }

  &:hover {
    span::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    outline: none;

    span::before {
      border-color: $c-purple;
    }
  }

  &:active,
  &:active:focus {
    span {
      color: $c-light;
    }

    span::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.result__form {
  margin-top: auto;
}

.result--trip,
.result--prize {
  .result__title {
    width: 57.3rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      width: 573px;
      height: 113px;
    }

    @media (orientation: portrait) {
      width: 28.7rem;
      height: 5.7rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      width: 40rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin-bottom: 0;
      width: 35rem;
    }
  }
}

.result--negative {
  .result__title {
    margin-top: -3.5rem;
    width: 80.5rem;
    height: 28.3rem;

    @media (min-width: $stop-scaling) {
      margin-top: 95px;
      width: 665px;
      height: 113px;
    }

    @media (orientation: portrait) {
      margin-top: 0;
      width: 28.8rem;
      height: 4.9rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      margin-top: 0;
      width: 50rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 45rem;
    }
  }
}


.screen--result.screen--show {

  .result__image-item--suitcase {
    animation: scale-in-bottom 0.45s cubic-bezier(.6,-0.25,.34,1.51) forwards;
  }

  .result__image-item--flamingo {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    animation: floating-flamingo 2s ease-in-out infinite 0.5s;
  }

  .result__image-item--leaf-green {
    opacity: 1;
    transform: rotate(0deg);
    animation: floating-leafs 2s ease-out both 0.3s;
  }

  .result__image-leaves--left {
    animation: floating-blue-leaves-left 2s ease-out 1 both 0.3s;
  }

  .result__image-leaves--right {
    animation: floating-blue-leaves-right 2s ease-out 1 both 0.2s;
  }

  .form--result {
    .form__field {
      width: calc(100% - 21rem);

      @media (max-width: $tablet) {
        width: calc(100% - 5rem);
      }
    }

    .form__button {
      opacity: 1;
    }
  }

}
